/** Catamaran Regular **/
@font-face {
  font-family: "Catamaran";
  font-weight: 300;
  font-style: normal;
  font-display: fallback;
  src: local("Catamaran"),
      url('files/catamaran-latin-300.woff2'),
      url('files/catamaran-latin-300.woff'),
      url("v4/Catamaran-Light.ttf");
}

/** Catamaran Regular **/
@font-face {
  font-family: "Catamaran";
  font-weight: 500;
  font-style: normal;
  font-display: fallback;
  src: local("Catamaran-Bold"),
      url('files/catamaran-latin-500.woff2'),
      url('files/catamaran-latin-500.woff'),
      url("v4/Catamaran-SemiBold.ttf");
}

/** Catamaran Regular **/
@font-face {
    font-family: "Catamaran";
    font-weight: 600;
    font-style: normal;
    font-display: fallback;
    src: local("Catamaran-Bold"),
        url('files/catamaran-latin-600.woff2'),
        url('files/catamaran-latin-600.woff'),
        url("v4/Catamaran-Bold.ttf");
}